.header {
  height: 160px;
  position: relative;
  overflow: hidden;
}

.header > div {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header > div > img {
  --offset: 0px;

  display: block;
  width: 110%; /* 由于有位移，稍为放大一点避免穿帮 */
  height: 110%;
  object-fit: cover; /* 按比例填满 */
  transform: scaleX(1.2) translateX(var(--offset)); /* 获取位置设定值 */
}
