/* navbar */
.nav-item .nav-link {
  padding: 0.5rem 0rem;
}
.navbar-nav .nav-item a {
  font-size: 18px;
  text-transform: capitalize;
}

.navbar-nav .nav-item a.is-active {
  text-decoration: underline;
}

/*skills list*/
.skills {
  list-style-type: none;
}

/*experiences*/
.experiences {
  padding-left: 16px;
}

.experiences .company {
  font-size: 26px;
  font-weight: bold;
}

.experiences .role {
  font-size: 15px;
  display: block;
  margin-bottom: 10px;
}

.experiences ul li {
  font-size: 20px;
  margin-bottom: 16px;
  line-height: 32px;
}
